.todo-items{
    justify-content: center;
    padding: 0;
    min-height: 500px;
    width: 70%;
    margin: 0 auto;
    font-size: 40px;
    list-style-type: none;
    margin-top: 5px;
    margin-bottom: 5px;

}

.todo-items input[type=checkbox]{
    margin-top: 30px;
    -webkit-appearance: none;
	-moz-appearance: none;
	display: inline-block;
	width: 64px;
	height: 64px;
	padding: 0;
	vertical-align: top;
	outline: none;
	transition: background 0.3s ease;
	background: url(data:image/png;base64,) 0 0 no-repeat;
	cursor: pointer;
}

.todo-items input[type=checkbox]:checked{
    background-image: url(data:image/png;base64,);
}

.todo-items li {
        /* width: 300px; */
        height: 100px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        border: 12px solid rgb(163, 118, 201) transparent;
        box-shadow: 0 0 8px rgba(0, 0, 0, 0.15);;
        border-radius: 10px ;
        color: rgba(7, 22, 88, 0.774);
        padding: 3px 3px;
        margin-top: 20px;
        margin-left: 3px;
        text-decoration: none;
        font-size: 15px;
        width: 95%;
        /* opacity: 0.; */
        word-break: break-word;
    }

    .input-li {
        width: 50%;
        border-radius: 10px;
        height: 30px;
        }

    .todo-items span {
        margin-top: 3%;
        margin-bottom: 2%;
        text-align: center;
        font-size: x-large;
        scrollbar-color: violet;
        justify-content: space-between;
        height: 60%;
        width: 60%;
        /* word-break: break-word; */
        text-overflow: ellipsis;
        overflow-wrap: break-word;
        overflow-x:hidden;
    }
    ::-webkit-scrollbar{
        width: 3px;
        opacity: 80%;
    }
    
    ::-webkit-scrollbar-thumb{
        
        background-color: rgb(133, 228, 235);
        width: 3px;
    }
